import Vue from "vue";
import Vuex from "vuex";
import http from "@/utils/request";
import {
  isEmptyObject,
  assign,
  shouldUpdateURL,
  isInteger,
} from "@/utils/common";
import Loading from "@/components/common/loading";
import Toast from "@/components/common/toast";
import { getChapterList, getChapterInfo } from "../service/reader";
import {
  storeChapterInfo,
  updateSlideBlockPos,
  saveCacheData,
} from "@/utils/chapter";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 
    showContents: false,
    // 
    showToolbar: false,
    // id
    cacheChapter: {},
    // 
    chapterInfo: {
      novelId: 0,
      chapterId: 0,
      contentList: [],
      chapterTitle: "",
      novelTitle: "",
      startChapterId: 0,
      endChapterId: 0,
    },
    // 
    novelInfo: {
      // 
      novelTitle: "",
      // 
      chapterList: [],
    },
    // 
    currentInfo: {
      // 
      pageNum: 1,
      // 
      bgColorNum: JSON.parse(localStorage.getItem("bgColor") || "1"),
      // 
      isDayMode: JSON.parse(localStorage.getItem("dayMode") || "true"),
      // 
      fontSize: "",
      //  1: 2 3 4
      lineHeight: JSON.parse(localStorage.getItem("textLineHeight") || "4"),
      // 
      leftMode: JSON.parse(localStorage.getItem("leftMode") || "false"),
      // 
      scrollTurnPage: JSON.parse(
        localStorage.getItem("scrollTurnPage") || "false"
      ),
      // 
      showMenu: false,
      // 
      activeMenu: "",
      // 
      totalPageNum: 0,
    },
    // slider
    chapterSliderInfo: {
      // 
      progress: 0,
      // 
      showProgressWindow: false,
    },
    // 
    containerInfo: {
      // translateX
      currentPos: 0,
      // 
      pageWidth: 0,
      // 
      containerWidth: 0,
      // dom
      $page: null,
      // 10
      turnPageMode: 1,
      // 
      canSlide: true,
    },
    // 
    verticalContainerInfo: {
      // 
      $page: null,
      // 
      $chapterContainer: null,
      // 
      chapterList: [],
      // 
      shouldScrollLoad: true,
    },
  },
  mutations: {
    updateContentsStatus(state) {
      state.showContents = !state.showContents;
    },

    updateToolBarStatus(state) {
      state.showToolbar = !state.showToolbar;
    },

    // 
    updateContainerInfo(state, payload) {
      state.containerInfo = assign(state.containerInfo, payload);
    },

    // 
    updateCurrentInfo(state, payload) {
      state.currentInfo = assign(state.currentInfo, payload);
    },

    // slider
    updateChapterSliderInfo(state, payload) {
      state.chapterSliderInfo = assign(state.chapterSliderInfo, payload);
    },

    // 
    updateChapterInfo(state, payload) {
      state.chapterInfo = assign(state.chapterInfo, payload);
    },

    // 
    updateNovelInfo(state, payload) {
      state.novelInfo = assign(state.novelInfo, payload);
    },

    // 
    updateCacheChapterInfo(state, payload) {
      state.cacheChapter = assign(state.cacheChapter, payload);
    },

    // 
    updateVerticalContainerInfo(state, payload) {
      state.verticalContainerInfo = assign(
        state.verticalContainerInfo,
        payload
      );
    },
  },
  actions: {
    // 
    getChapterInfo({ dispatch, state }, params) {
      const { scrollTurnPage } = state.currentInfo;
      const { startChapterId, endChapterId } = state.chapterInfo;
      const { chapterId } = params;

      // 
      if (startChapterId && chapterId < startChapterId) {
        return Toast("");
      }

      if (endChapterId && chapterId > endChapterId) {
        return Toast("");
      }

      // 
      if (scrollTurnPage) {
        dispatch("getVerticalChapterInfo", params);
      } else {
        dispatch("getHorizontalChapterInfo", params);
      }
    },

    // 
    getVerticalChapterInfo({ state, commit, dispatch }, params) {
      /**
       * scrollLoad
       */
      const {
        novelId,
        chapterId,
        replacePath = true,
        showLoading = true,
        scrollLoad = false,
        pullLoad = false,
        callback,
      } = params;
      let { chapterList, $page, $chapterContainer } =
        state.verticalContainerInfo;

      showLoading && Loading.show();

      new Promise((resolve) => {
        // 
        const cacheChapter = state.cacheChapter[chapterId];
        if (cacheChapter) {
          commit("updateChapterInfo", cacheChapter);
          if (scrollLoad) {
            chapterList.push(cacheChapter);
          } else if (pullLoad) {
            chapterList.unshift(cacheChapter);
          } else {
            chapterList = [cacheChapter];
          }
          // 
          commit("updateVerticalContainerInfo", { chapterList });
          console.log("vertical");
          resolve();
        } else {
          // 
          //   http
          //     .get("/chapterInfo", {
          //       params: {
          //         novelId,
          //         chapterId,
          //       },
          //     })
          getChapterInfo({ novelId, chapterId })
            .then((res) => {
              if (!isEmptyObject(res.data)) {
                commit("updateChapterInfo", res.data);
                if (scrollLoad) {
                  chapterList.push(res.data);
                } else if (pullLoad) {
                  chapterList.unshift(res.data);
                } else {
                  chapterList = [res.data];
                }
                commit("updateVerticalContainerInfo", { chapterList });
                // 
                saveCacheData({ commit, chapterId, data: res.data });
                console.log("vertical");
                resolve();
              } else {
                // TODO: 
              }
            })
            .catch((err) => {
              console.log(
                err,
                "Happened in the function of request verticalChapterInfo..."
              );
            });
        }
      })
        .then(() => {
          let { startChapterId, endChapterId } = state.chapterInfo;

          // 
          callback && callback();

          // url
          shouldUpdateURL({
            replacePath,
            novelId,
            chapterId,
          });

          // idkey
          storeChapterInfo({
            novelId,
            chapterId,
            pageNum: 1,
          });

          // slider
          updateSlideBlockPos({
            commit,
            chapterId,
            endChapterId,
            startChapterId,
          });

          // 
          if (!scrollLoad && !pullLoad) {
            $page.scrollTo(0, 0);
          } else if (pullLoad) {
            // 
            const firstChapterBoxHeight = document.querySelector(
              ".reader-page__viewport-vertical__chapter"
            ).clientHeight;
            $page.scrollTo(0, firstChapterBoxHeight);
          }

          // 
          if ($chapterContainer.clientHeight <= $page.clientHeight) {
            dispatch("getChapterInfo", {
              novelId,
              chapterId: chapterId + 1,
              scrollLoad: true,
            });
          }

          Loading.close();
        })
        .catch((err) => {
          console.log(
            err,
            "Happened in getVerticalChapterInfo Promise catch function..."
          );
        });
    },

    // 
    getHorizontalChapterInfo({ state, commit }, params) {
      // backEndPage, replacePathrouterpath
      const {
        novelId,
        chapterId,
        backEndPage = false,
        replacePath = true,
      } = params;

      // idreturn
      if (chapterId === state.chapterInfo.chapterId) {
        return;
      }

      // 
      commit("updateContainerInfo", { canSlide: false });

      new Promise((resolve, reject) => {
        // loading
        Loading.show();

        // 
        const cacheChapter = state.cacheChapter[chapterId];
        if (cacheChapter) {
          // 
          commit("updateChapterInfo", cacheChapter);
          console.log("");
          resolve();
        } else {
          // 
          //   http
          //     .get("/chapterInfo", {
          //       params: {
          //         novelId,
          //         chapterId,
          //       },
          //     })
          getChapterInfo({ novelId, chapterId })
            .then((res) => {
              if (!isEmptyObject(res.data)) {
                commit("updateChapterInfo", res.data);
                // 
                saveCacheData({ commit, chapterId, data: res.data });
                console.log("");
                // then
                resolve();
              } else {
                // TODO: 
                reject();
              }
            })
            .catch((err) => {
              console.log(
                err,
                "Happened in the function of request chapterInfo..."
              );
            });
        }
      })
        .then(() => {
          // url
          shouldUpdateURL({
            replacePath,
            novelId,
            chapterId,
          });

          // 
          const { pageWidth, $page } = state.containerInfo;
          const { startChapterId, endChapterId } = state.chapterInfo;
          const containerWidth = $page.scrollWidth;
          const calculatePageNum = containerWidth / pageWidth;
          // 
          const totalPageNum = isInteger(calculatePageNum)
            ? calculatePageNum
            : Math.floor(calculatePageNum);

          // 
          commit("updateCurrentInfo", {
            totalPageNum,
            pageNum: backEndPage ? totalPageNum : 1,
          });

          // slider
          updateSlideBlockPos({
            commit,
            chapterId,
            endChapterId,
            startChapterId,
          });

          // 
          commit("updateContainerInfo", {
            currentPos: backEndPage ? pageWidth - containerWidth : 0,
            containerWidth,
            // 
            canSlide: true,
          });

          // 
          $page.style.transitionDuration = "0s";
          $page.style.transform = `translateX(${
            backEndPage ? -containerWidth + pageWidth : 0
          }px)`;

          // idkey
          storeChapterInfo({
            novelId,
            chapterId,
            pageNum: backEndPage ? totalPageNum : 1,
          });

          // loading
          Loading.close();
        })
        .catch((err) => {
          console.log(
            err,
            "Happened in getChapterInfo Promise catch function..."
          );
        });
    },

    // 
    getChapterListInfo({ commit }, params) {
      getChapterList({
        params,
      })
        .then((res) => {
          commit("updateNovelInfo", res.data);
        })
        .catch((err) => {
          console.log(err, "Happened in getChapterInfo function");
        });

      //   http
      //     .get("/chapterList", {
      //       params,
      //     })
      //     .then((res) => {
      //       commit("updateNovelInfo", res.data);
      //     })
      //     .catch((err) => {
      //       console.log(err, "Happened in getChapterInfo function");
      //     });
    },
  },
  modules: {},
});
