import Vue from 'vue';
import MyToast from './toast.vue';

// TODO: iconloading
const ToastConstructor = Vue.extend(MyToast);
const toastInstance = [];
// toast
const defaultConfig = {
    // 
    duration: 2000,
    // 
    message: '',
    // toastcallback
    onClose: null,
    // toast
    position: 'middle'
}

// toast toast
function getInstance() {
    if (toastInstance.length > 0) {
        let instance = toastInstance[0];
        return instance
    }

    return new ToastConstructor()
}

// toast
function getToastNode() {
    return document.getElementById('toast');
}

/**
 * toast
 * @param {Node} $node toast 
 */
function removeToastNode($node) {
    document.body.removeChild($node)
}

// toastnode
ToastConstructor.prototype.close = function () {
    this.closed = true;
    this.visible = false;
}

/**
 * toast
 * @param {Object} options 
 * @return {Object} toast
 */
const Toast = (options = {}) => {
    // toast
    const $toast = getToastNode();
    if ($toast) {
        removeToastNode($toast);
    }

    // toast
    let instance = getInstance();
    // 
    clearTimeout(instance.timer);

    // optionsmessage
    if (typeof options === 'string') {
        options = {
            message: options
        }
    }

    // 
    options = { ...defaultConfig, ...options};
    const { duration, message, onClose } = options;

    instance.closed = false;
    // dom
    instance.vm = instance.$mount();
    instance.$dom = instance.vm.$el;
    // toast
    instance.unMount = removeToastNode;
    instance.onClose = onClose;
    instance.message = message;
    instance.visible = true;
    // 
    document.body.appendChild(instance.$dom);
    toastInstance.length === 0 && toastInstance.push(instance);

    Vue.nextTick(function () {
        // toast
        instance.timer = setTimeout(() => {
            if (instance.closed) {
                return 
            }

            instance.close();
        }, duration);
    })

    return instance
}

export default Toast
